*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container{
  padding: 3rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  background-color: antiquewhite;
    h1{
    color: rgb(79, 2, 2);
    text-transform: uppercase;
    }
}
.categories{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.categories button{
  padding: 10px 1rem;
  border-radius: 10px;
  text-transform: uppercase;
  border: none;
  border: 1px solid black;
  background-color: rgba(242, 9, 9, 0.328);
  &:hover{
    cursor: pointer;
    background-color: brown;
    color: white;
  }
}

.products{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
    .product{
      width: 350px;
      height: 500px;
      position: relative;
      overflow: hidden;
      &:hover .title{
        transform: translateX(-100%);
        opacity: 1;
       }
        img{
         width: 100%;
         height: 100%;
         border-radius: 20px;
         box-shadow: 2px 3px 15px black;
         opacity: 0.5;
         transition: all 1s;
         &:hover{
          opacity: 1;
         }
       }
       .price{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        font-weight: bold;
        background-color: rgb(104, 62, 62);
        color: white;
        padding: 1rem;
        border-radius: 10px;
       }
       .title{
        display: block;
        position: absolute;
        left: 350px;
        bottom: 20px;
        background-color: rgb(104, 62, 62);
        color: white;
        width: 100%;
        text-align: center;
        font-size: 1.5rem;
        padding: 10px;
        border-radius: 10px;
        transition: all 1s;
        opacity: 0;
       }
    }
}